button {
    cursor:pointer;
}

.site-header {
    width: 100vw;
    height: auto;
}

.site-nav {
    position: absolute;
    top: 2%;
    left: 5%;
}

.home-page, .about {
    color: white;
}

.contact-search {
    position: absolute;
    top: 2%;
    right: 5%;
}

.site-footer {
    height: 30vh;
    width: 100vw;
    background-color: gray;
}

.search-button {
    border-radius: 50%;
    text-decoration: none;
    background-color: #B500B5;
    border: 0px solid #B500B5;
    width: 28px;
    height: 28px;
    font-size: 18px;
    text-align: center;
    outline: none;
}

.magnifying-glass {
    -webkit-transform: rotate(-45deg); 
    -moz-transform: rotate(-45deg); 
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
    color: white;
}

.fa {
    padding-top: .4em;
    width: 35px;
    height: 35px;
    text-align: center;
    text-decoration: none;
    border-radius: 50%;
    border: 1px solid white;
}

// .fa:hover {
//     opacity: 0.5;
// }

.fa-facebook, .fa-instagram, .fa-linkedin, .fa-snapchat-ghost, .fa-twitter, .fa-youtube-play {
    color: white;
}

.slogan {
    color: white;
}

.legal-sites {
    color: white;
}

a {
    text-decoration: none;
    color: white;
}

#relative-link {
    color: lightgray;
}